'use client';

import Image from 'next/image';
import { useEffect, useState } from 'react';
import Slider from '@/components/Slider';
import ProductsList from '@/components/ProductsList';
import ReviewsSummary from '@/components/ReviewsSummary';
import PromoCodes from '@/components/PromoCodes';
import About from './about/page';
import Contact from './contact/page';

import { categories } from '@/data/categories';
import { scrollToProducts, scrollToSection, initializeDefaultScrolling } from '@/utils/scroll';
import { useSearch } from '@/contexts/SearchContext';

const HomePage = () => {
  const [selectedCategory, setSelectedCategory] = useState<string | undefined>();
  const { searchQuery } = useSearch();

  useEffect(() => {
    // Initialize default scrolling behavior
    const cleanup = initializeDefaultScrolling();
    
    // Listen for the clear category and show all products event
    const handleClearCategoryAndShowAll = () => {
      setSelectedCategory(undefined);
    };

    window.addEventListener('clearCategoryAndShowAll', handleClearCategoryAndShowAll);
    
    return () => {
      window.removeEventListener('clearCategoryAndShowAll', handleClearCategoryAndShowAll);
      if (cleanup) cleanup();
    };
  }, []);

  const scrollToSectionHandler = (sectionId: string) => {
    const element = document.getElementById(sectionId);
    if (element) {
      if (sectionId === 'home') {
        setSelectedCategory(undefined);
        // Small delay to ensure state is updated
        requestAnimationFrame(() => {
          scrollToProducts();
        });
      } else {
        // Use default scrolling utility
        scrollToSection(sectionId);
      }
    }
  };

  // Handle initial scroll if URL has a hash - removed duplicate useEffect
  useEffect(() => {
    if (window.location.hash) {
      const id = window.location.hash.substring(1);
      setTimeout(() => scrollToSectionHandler(id), 300);
    }
  }, []);

  return (
    <div className="flex flex-col min-h-screen">
      <main className="flex-1">
        {/* Hero Section */}
        <div id="home" className="px-4">
          <Slider />
        </div>

        {/* Featured Categories Section */}
        <section id="products" className="py-8 sm:py-16 bg-gray-50">
          <div className="container mx-auto px-4">
            <h2 className="text-2xl sm:text-3xl font-bold text-center mb-2 sm:mb-4">Categories</h2>
           
            {/* Mobile - Horizontal scroll layout */}
            <div className="sm:hidden">
              <div className="flex overflow-x-auto space-x-4 px-4 py-4 scrollbar-hide">
                {categories.map((category, idx) => (
                  <div 
                    key={idx}
                    className={`flex flex-col items-center group cursor-pointer transition-all duration-300 min-w-[80px] ${
                      selectedCategory === category.title ? 'opacity-100 scale-105' : 'opacity-90 hover:opacity-100'
                    }`}
                    onClick={() => {
                      setSelectedCategory(category.title);
                      scrollToProducts();
                    }}
                  >
                    <div className={`relative w-16 h-16 mb-2 overflow-hidden rounded-full shadow-lg transform transition-all duration-300 hover:scale-110 hover:shadow-xl ${
                      selectedCategory === category.title ? 'ring-1 ring-orange-500 ring-offset-1' : ''
                    }`}>
                      <Image
                        src={category.img}
                        alt={category.title}
                        fill
                        sizes="64px"
                        quality={85}
                        priority={idx < 3}
                        className={`object-cover rounded-full transform transition-all duration-300 ${
                          selectedCategory === category.title ? 'brightness-110 scale-105' : 'hover:brightness-110'
                        }`}
                      />
                    </div>
                    <h3 className="text-xs font-bold text-gray-800 text-center whitespace-nowrap">{category.title}</h3>
                  </div>
                ))}
              </div>
            </div>

            {/* Desktop - Grid layout */}
            <div className="hidden sm:grid grid-cols-3 md:grid-cols-4 lg:grid-cols-5 xl:grid-cols-6 gap-4 sm:gap-6 md:gap-8 px-4 sm:px-6 py-4">
              {categories.map((category, idx) => (
                <div 
                  key={idx}
                  className={`flex flex-col items-center group cursor-pointer transition-all duration-300 ${
                    selectedCategory === category.title ? 'opacity-100 scale-105' : 'opacity-90 hover:opacity-100'
                  }`}
                  onClick={() => {
                    setSelectedCategory(category.title);
                    scrollToProducts();
                  }}
                >
                  <div className={`relative w-28 h-28 md:w-36 md:h-36 lg:w-40 lg:h-40 xl:w-44 xl:h-44 mb-3 sm:mb-4 md:mb-5 overflow-hidden rounded-full shadow-lg sm:shadow-xl transform transition-all duration-300 hover:scale-110 hover:shadow-xl ${
                    selectedCategory === category.title ? 'ring-1 sm:ring-2 ring-orange-500 ring-offset-1 sm:ring-offset-2' : ''
                  }`}>
                    <Image
                      src={category.img}
                      alt={category.title}
                      fill
                      sizes="(max-width: 768px) 112px, (max-width: 1024px) 144px, (max-width: 1280px) 160px, 176px"
                      quality={85}
                      priority={idx < 3}
                      className={`object-cover rounded-full transform transition-all duration-300 ${
                        selectedCategory === category.title ? 'brightness-110 scale-105' : 'hover:brightness-110'
                      }`}
                    />
                    <div className="absolute inset-0 bg-black/40 opacity-0 group-hover:opacity-100 transition-opacity duration-300 rounded-full flex items-center justify-center">
                      <span className="text-white text-xs sm:text-sm md:text-base font-medium px-2 sm:px-4 text-center">View</span>
                    </div>
                  </div>
                  <h3 className="text-sm md:text-base font-bold text-gray-800 text-center">{category.title}</h3>
                </div>
              ))}
            </div>
          </div>
        </section>

        {/* Products Section */}
        <section id="all-products" className="py-8 sm:py-16">
          <div className="max-w-7xl mx-auto px-4">
            <div className="space-y-6">
              {/* Search Section */}


              <div className="flex justify-between items-center mb-6">
                <h2 className="text-2xl sm:text-3xl font-bold text-gray-800 flex-1">
                  {searchQuery ? `Search Results` : selectedCategory ? `${selectedCategory} Products` : 'Our Products'}
                </h2>
              </div>
              
              {/* Display search query info */}
              {searchQuery && (
                <div >
                  <div >
                    <span className="text-orange-800">
                      Showing results for: <strong>&ldquo;{searchQuery}&rdquo;</strong>
                    </span>

                  </div>
                </div>
              )}
              
              <ProductsList selectedCategory={selectedCategory} />
            </div>
          </div>
        </section>

        {/* About Section */}
        <section id="about" className="py-8 sm:py-16 bg-gray-50">
          <About />
        </section>

        {/* Contact Section */}
        <section id="contact" className="py-8 sm:py-16">
          <Contact />
        </section>
      </main>
    </div>
  );
};

export default HomePage;
