// Amazon-style reviews data structure
export interface Review {
  id: string;
  productId: string;
  userId: string;
  userName: string;
  userAvatar?: string;
  rating: number; // 1-5 stars
  title: string;
  comment: string;
  dateCreated: Date;
  verified: boolean;
  helpful: number;
  notHelpful: number;
  images?: string[];
  vineCustomer?: boolean;
  topReviewer?: boolean;
  earlyReviewer?: boolean;
}

export interface PromoCode {
  id: string;
  code: string;
  description: string;
  discount: number;
  type: 'percentage' | 'fixed';
  minOrder?: number;
  validUntil: Date;
  isActive: boolean;
  usageLimit?: number;
  usedCount: number;
}

