// Helper functions
export const getReviewsForProduct = (productId: string): Review[] => {
  return reviewsData.filter(review => review.productId === productId);
};

export const getAverageRating = (productId: string): number => {
  const reviews = getReviewsForProduct(productId);
  if (reviews.length === 0) return 0;
  
  const totalRating = reviews.reduce((sum, review) => sum + review.rating, 0);
  return Math.round((totalRating / reviews.length) * 10) / 10;
};

export const getReviewCount = (productId: string): number => {
  return getReviewsForProduct(productId).length;
};

export const getRatingDistribution = (productId: string): { [key: number]: number } => {
  const reviews = getReviewsForProduct(productId);
  const distribution: { [key: number]: number } = { 5: 0, 4: 0, 3: 0, 2: 0, 1: 0 };
  
  reviews.forEach(review => {
    distribution[review.rating]++;
  });
  
  return distribution;
};

export const getRecommendationPercentage = (productId: string): number => {
  const reviews = getReviewsForProduct(productId);
  if (reviews.length === 0) return 0;
  
  const positiveReviews = reviews.filter(review => review.rating >= 4).length;
  return Math.round((positiveReviews / reviews.length) * 100);
};

export const getActivePromoCodes = (): PromoCode[] => {
  return promoCodesData.filter(promo => 
    promo.isActive && 
    promo.validUntil > new Date() &&
    (promo.usageLimit ? promo.usedCount < promo.usageLimit : true)
  );
};

export const getPromoCodesByProduct = (productId: string): PromoCode[] => {
  // In a real app, you might filter by product category or specific products
  return getActivePromoCodes();
};

// Sort options for reviews
export type ReviewSortOption = 
  | 'helpful' 
  | 'newest' 
  | 'oldest' 
  | 'highest' 
  | 'lowest' 
  | 'verified';

export const sortReviews = (reviews: Review[], sortBy: ReviewSortOption): Review[] => {
  const sorted = [...reviews];
  
  switch (sortBy) {
    case 'helpful':
      return sorted.sort((a, b) => b.helpful - a.helpful);
    case 'newest':
      return sorted.sort((a, b) => new Date(b.dateCreated).getTime() - new Date(a.dateCreated).getTime());
    case 'oldest':
      return sorted.sort((a, b) => new Date(a.dateCreated).getTime() - new Date(b.dateCreated).getTime());
    case 'highest':
      return sorted.sort((a, b) => b.rating - a.rating);
    case 'lowest':
      return sorted.sort((a, b) => a.rating - b.rating);
    case 'verified':
      return sorted.sort((a, b) => (b.verified ? 1 : 0) - (a.verified ? 1 : 0));
    default:
      return sorted;
  }
};
